.flash-messages {
  position: fixed;
  z-index: 999999;
  pointer-events: none;
  top: 12px;
  right: 12px;
}
.activate-btn {
  min-width: 142.267px!important;
}
.sidebar {
  .nav-dropdown-items {
    .nav-item {
      a {
        padding-left: 43px;
      }
    }
  }
}
.create-card .mat-card {
  padding: 0;
  .mat-card-header {
    background: rgba(0,0,0,.03);
    align-content: center;
    align-items: center;
    display: flex;
    justify-content: center;
    padding: 8px 20px;
    min-height: 56px;
    .mat-card-title {
      color: rgba(0,0,0,.54);
      font-weight: 400;
      font-size: 18px;
      margin-bottom: 0;
    }
    .mat-card-header-text {
      flex: 1 1;
    }
  }
  .mat-card-content {
    margin-bottom: 0;
  }
  .mat-card-footer {
    padding: 5px 30px;
    margin: 0;
  }
  .mat-card-padding {
    padding: 30px;
  }
}
.mat-tab-label {
  font-weight: 500;
  text-transform: uppercase;
}
.action-btn button {
  margin-right: 8px;
}
.spinner-container {
  width: 100%;
  position: absolute;
  top: 21%;
}
.spinner-container mat-spinner {
  margin: 130px auto 0 auto;
}
.main .container-fluid {
  padding: 0 30px;
}
.mat-form-field-infix {
  width: 50px!important;
}
.mat-paginator-page-size-label {
  min-width: 100px!important;
}
.table-empty {
  text-align: center;
  padding: 15px;
  border-bottom: 1px solid rgba(0,0,0,.12);
}
.tr-hover tr:hover {
  background-color: rgba(0, 0, 0, 0.04);
}
.td-link {
  cursor: pointer;
}
.input-full-width {
  width: 100%;
}
.list-table {
  table {
    width: 100%;
  }
  .mat-form-field {
    font-size: 14px;
    width: 100%;
  }
}
.ck-editor__editable_inline {
  min-height: 350px;
}
.editor-label {
  color: #808080;
}
.checkbox-box {
  align-content: center;
  align-items: center;
  height: 45px;
}
.select-language-dropdown, #select-language {
  .flag-icon {
    font-size: 16px;
    margin-right: 11px;
  }
  .dropdown-item {
    cursor: pointer;
  }
}
.header-right-menu {
  li {
    margin-right: 25px!important;
  }
  .notification-item {
    margin-top: 10px;
  }
}
.notification-item {
  .dropdown-item {
    cursor: pointer;
  }
}
.card-header {
  padding: .75rem 1.25rem;
  margin-bottom: 0;
  border-bottom: 1px solid;
  border-bottom-color: currentcolor;
  border-bottom-color: currentcolor;
  background-color: #fff;
  border-color: #d8dbe0;
}
.ng-dirty.ng-invalid {
  border-color: #f86c6b;
}
dropzone > .dropzone.dz-wrapper .dz-message {
  margin-right: 0!important;
  margin-left: 0!important;
  width: 100%!important;
  min-height: 200px!important;
  max-width: 300px!important;
}
dropzone > .dropzone.dz-wrapper.dz-single .dz-preview {
  max-width: 280px!important;
  height: auto!important;
  width: 100% !important;
}
dropzone > .dropzone.dz-wrapper {
  max-width: 300px!important;
}
.help-block {
  font-size: 12px;
  color: #a6a6a6;
  padding-top: 6px;
}
.invalid-feedback {
    display: block!important;
}
