.select2-container {
  display: block;
  width: auto !important;
}
.select2-container .select2-selection {
  border: 1px solid;
  border-top-color: currentcolor;
  border-right-color: currentcolor;
  border-bottom-color: currentcolor;
  border-left-color: currentcolor;
  border-radius: .25rem;
  width: 100%;
  background-color: #fff;
  border-color: #e4e7ea;
}
.select2-container .select2-selection--single {
  height: calc(1.5em + .75rem + 2px) !important;
}
.select2-container .select2-results > .select2-results__options {
  max-height: 15em;
  overflow-y: auto;
}
.select2-container .select2-dropdown .select2-results__option[aria-selected="true"] {
  background-color: #f2f2f2;
}
.select2-container .select2-results__option--highlighted, .select2-container .select2-results__option--highlighted.select2-results__option[aria-selected="true"] {
  color: #ebedef;
}
.select2-container .select2-results__option--highlighted, .select2-container .select2-results__option--highlighted.select2-results__option[aria-selected="true"] {
  background-color: #321fdb!important;
  color: #ebedef!important;
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  line-height: 35px;
}
.select2-container .select2-selection--multiple .select2-selection__choice {
  border: 1px solid;
  border-top-color: currentcolor;
  border-right-color: currentcolor;
  border-bottom-color: currentcolor;
  border-left-color: currentcolor;
  border-radius: .2rem;
  padding: 0;
  padding-right: 0px;
  padding-right: 5px;
  cursor: pointer;
  float: left;
  margin-top: 0.3em;
  margin-right: 5px;
  color: #5c6873;
  border-color: #39f;
  background-color: #fff;
}
.select2-container .select2-selection--multiple .select2-selection__choice__remove {
  width: 26px;
  cursor: pointer;
  display: inline-block;
  font-weight: bold;
  text-align: center;
  margin-right: .375rem;
  border-right: 1px solid;
  border-right-color: currentcolor;
  background: #fff;
  color: #9cf;
  border-color: #39f;
  background: rgba(51,153,255,0);
}
.ng-dirty.ng-invalid .select2-container .select2-selection {
  border-color: #f86c6b;
}
